import { MUSICIANS as musicians } from "@constants";
import { MUSIC_EVENTS as DATA } from "data-raw";
import { Musician } from "types";
import { getItemCounts, detailsContainsFavourite } from "utils";

const getMusicianMatches = (musician: Musician): MusicEvent[] =>
  DATA.reduce(
    (matches: MusicEvent[], musicEvent: MusicEvent): MusicEvent[] =>
      musicEvent.musicians.includes(musician)
        ? [...matches, musicEvent]
        : matches,
    []
  );

const getMusicianFestivalDate = (
  musician: Musician,
  { lineup, dates }: Festival
): Date => {
  if (lineup.length === 1) return dates[0];
  for (let day = 0; day < lineup.length; day++) {
    if (lineup[day].includes(musician)) return dates[day];
  }
  alert(
    "There has been an error calculating which day you saw a musician at a festival on"
  );
  return new Date();
};

const getMusicianDetails = (musician: Musician): MiniCardPanelDetailProps[] =>
  getMusicianMatches(musician).map(
    ({
      dates,
      festival,
      gig,
      musicians,
      venue,
      favourite,
      video
    }: MusicEvent): MiniCardPanelDetailProps => ({
      mainText: [festival ? festival.title.name : venue.name],
      sort: [dates[0]],
      dates: festival ? [getMusicianFestivalDate(musician, festival)] : dates,
      video: gig
        ? gig.headline === musician
          ? video
          : undefined
        : festival && musicians.includes(musician)
        ? video
        : undefined,
      favourite: favourite && gig && gig.headline === musician
    })
  );

const mapMusicianToMiniCard = (musician: Musician): MiniCardProps => {
  const { name, previousStageName, noLongerExists } = musician;
  const details: MiniCardPanelDetailProps[] = getMusicianDetails(musician);
  const {
    pastCount: primaryCount,
    futureCount: secondaryCount
  } = getItemCounts({
    item: { musician },
    data: { musicEvents: DATA }
  });
  return {
    primaryText: name,
    primaryTextAlt: previousStageName && previousStageName.name,
    sort: [name],
    favourite: detailsContainsFavourite(details),
    primaryCount,
    secondaryCount,
    noLongerExists,
    details
  };
};

export const MUSICIANS: MiniCardProps[] = Object.values(musicians).map(
  mapMusicianToMiniCard
);
